import React, {useEffect, useState} from 'react';
import './App.css';
import { makeStyles } from '@material-ui/core/styles';
import GridList from "@material-ui/core/GridList";
import GridListTile from "@material-ui/core/GridListTile";
import GridListTileBar from "@material-ui/core/GridListTileBar";
import ListSubheader from "@material-ui/core/ListSubheader";

import database from "./database";

const useStyles = makeStyles(theme => ({
  root: {
    display: 'flex',
    flexWrap: 'wrap',
    justifyContent: 'space-around',
    overflow: 'hidden',
    backgroundColor: theme.palette.background.paper,
  },
  gridList: {
    transform: 'translateZ(0)'
  }
}));

function generateFighters(num = 10) {
  const generated = [];

  const checkedIdx = {};

  while (generated.length < num) {
    let idx;
    do {
      idx = Math.floor(Math.random() * database.length);
      console.log(idx);
    } while (checkedIdx[idx]);
    checkedIdx[idx] = true;

    generated.push(database[idx]);
  }

  return generated;
}

function App() {
  const styles = useStyles();
  const [fighters, setFighters] = useState([]);

  useEffect(() => {
    setFighters(generateFighters());
  }, []);

  return (
    <div className={styles.root}>
      <GridList cellHeight={130} spacing={1} className={styles.gridList}>
        <GridListTile key="Subheader" cols={2} style={{ height: 'auto' }}>
          <ListSubheader component="div">Random Set Of Characters</ListSubheader>
        </GridListTile>
        {fighters.map((fighter => {
          return (
            <GridListTile key={fighter.key}>
              <img src={`https://www.smashbros.com/assets_v2/img/fighter/thumb_a/${fighter.key}.png`}/>
              <GridListTileBar
                title={fighter.title}
                subtitle={fighter.series}
              />
            </GridListTile>
          );
        }))}
      </GridList>
    </div>
  );
}

export default App;
